#searchBar {
  margin: 1em;
}

#searchBar form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;

  width: 35em;
}

/* #searchBar div { */
/*   padding: 0 1em; */
/* } */

#searchBar label {
  display: inline-block;
  -ms-flex: 1 1;
      flex: 1 1;

  margin: 0.5em;
  text-align: right;

  font-size: 0.9em;
}

/* input, select */

#searchBar input {
  width: 25em;
}

#searchBar select {
  max-width: 20em;
}

#searchBar .add-sub-query {
  height: 2em;
  width: 2em;

  margin: 0 0.5em;
  padding: 0;
  border-radius: 50%;

  font-weight: bold;
  text-align: center;

  -webkit-transition: background-color 0.3s;

  -o-transition: background-color 0.3s;

  transition: background-color 0.3s;
}

#searchBar .add-query {
  background-color: #3f51b5;
  color: white;
}

#searchBar .add-query:hover {
  background-color: #2c387e;
}

#searchBar .add-query:active {
  background-color: #2c387e;
}

#searchBar .sub-query {
  background-color: #f73378;
  color: white;
}

#searchBar .sub-query:hover {
  background-color: #f50057;
}

#searchBar .sub-query:active {
  background-color: #ab003c;
}



